*{

    box-sizing: border-box;
    padding: 0;
    margin: 0px;
    font-family: 'Bebas Neue', cursive;

}


.menu-bar{
    width: 100%;
    height: 75px;
    background-color: rgb(48, 48, 48);
    margin:0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

}

.menu-nombre{
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    align-items: center;
    text-align: right;
    font-size: 1.6rem;
    padding: 0;
    text-align: center;
    width: auto;


}

.menu-nombre h3{
    
    color: #7BBEBE;
    margin:0px;
    margin:0;
    margin-right: 10px;
    padding: 0;
    font-size: 30px;
line-height:30px


}

.menu-nombre a{
    
    color: #7BBEBE;
    margin:0px;
    font-weight: 200;
    margin:0;
    margin-right: 10px;
    padding: 0;
    width: 40px;
    height: 40px;


}





.menu-redes{
    align-items: center;
    width:30%;
    display: flex;
    justify-content: space-evenly;
    list-style: none;
}



.icono{
    margin:10px;
    padding: 0;
    width: 40px;
    height: 40px;

    

}

.logo_icon{
    width: auto;
    height: 50px;
   
   
    

}


.icono:hover{
    transform: scale(1.2);
    transition: all .5s;

}

