    /* inicio @media 750 a 960px */

    @media all and (min-width:750px) and (max-width:960px){
      
      
        

        .menu-bar{
            border-radius: 0px 0px 10px 10px;
        
        }
        
        
        .menu-nombre h3{
            font-size: 1.3rem;
        }
        
        .menu-redes{
            align-items: center;
            width:30%;
            display: flex;
            justify-content: space-evenly;
            list-style: none;
        }
        
        
        
        .icono{
            width: 30px;
            height: 30px;
            padding: 0;
        
        }
        .logo_icon{
            height: 40px;
            
        
        }
        
            }
