body, #root{
    box-sizing: border-box;
   margin:0;
   padding:0;
width: 100%;
height: auto;
margin-left: 0;
margin-right: 0;


background: #414141;
font-family: 'Bebas Neue', cursive;
}


.black-home{
    position: relative;
    width: 80%;
    background: #131313;
    border-radius: 20px ;
    z-index: 0;
    padding: 40px;
    margin: 0 auto;
    margin-bottom:20px;

    
        
    }
    

    .fondo_home{
        position: absolute;
        width:100%;
        z-index: -1;

        top: 0px;
        right:0;
        height: auto;
        border-radius: 20px 20px 0px 0px;
    }

    .perfil{
        width: 100%;
        height:auto;
        
        display:flex;
        justify-content: space-around;
    align-items: center;
    margin:0;
    padding:0;
    }
    .caja-foto{
    background-color:#7BBEBE;
        border-radius:50%;
        width: 200px;
        height:200px;
        display:flex;
        justify-content: space-around;
    align-items: center;
        margin-top: 70px;
        
    }
    .foto-perfil{
    border-radius: 50%;
    height: 95%;
    width: 95%;
    background-color:#293b3b;

    
}

    .h2_black-home{

        width: 350px;
        text-align: right;
        float: right;
        margin-right: 40px;
        font-family: 'Carter One', cursive;
        font-weight: normal;
        font-size: 29.2881px;
        line-height: 122.5%;
        color: #7BBEBE;
        z-index: 3;
        background-color: #131313ad;
        padding: 15px ;
        border-radius: 5px;
        }
    
    .p_black-home{
        
        width: 60%;
        position:relative ;
        font-weight: normal;
        font-size: 3.8rem;
        line-height: 79.5%;
        text-align: right;
        color: #D8D8D8;
        margin-bottom:10px;
        margin-top: 100px;
        size-adjust: 100%;
        scroll-behavior: auto;
        
    
    
    
    }
    .black-contact{
        position: relative;
        width: 80%;
        height: auto;
        background: #131313;
        border-radius: 20px ;
        z-index: 0;
        padding: 20px;
        margin: 0 auto;
        margin-bottom:40px;
           
            
        }
   
    

    .fondo_contact{
        position: absolute;
        width:auto;
        height: 80%;
        z-index: -1;
        transform: scaleX(-1); 

        top: 0px;
        right:0;
        border-radius: 20px 0px 0px 0px;
    }
    

    .contact-tit{
        position: relative;
        float: right;
        margin:0;
        margin-right: 8%;
        top: 10px;
        text-align: right;
        color: #9ED952;
        font-style: normal;
        font-weight: normal;
        font-size: 3rem;
        background-color: #131313;
        padding:0px 15px;
        border-radius: 5px;
        z-index: 2;
        margin-bottom:20px;
    
    }

    .div_contact{
        display:flex;
        justify-content: center;
        position: relative;
        width: 100%;
        height:auto;
        
        
        
    }
    .p_black-contact{
        
        width: auto;
        font-weight: normal;
        font-size: 2rem;
        line-height: 79.5%;
        text-align: center;
        color: rgb(238, 255, 0);
        margin-bottom:5px;
        margin-top: 5px;
        background-color: #131313ad;
        padding: 15px ;
        border-radius: 5px;

        
        
    
    
    
    }

    .p_black-contact:hover{
            cursor:pointer; 
            color: #9ED952;
            transition: .25s ease all;
            transform: scale(1.2);


    }



