    /* inicio @media 750 a 960px */

    @media all and (min-width:750px) and (max-width:960px){

         
            
  

        .black-home{
            width: 100%;
            }
            .caja-foto{
                width: 160px;
                height:160px;
            }
        
            .h2_black-home{
        
                width: 200px;
                margin-right: 20px;
                font-family: 'Carter One', cursive;
                font-size: 25px;
                }
            
            .p_black-home{
                
                font-size: 3rem;
                line-height: 79.5%;
                text-align: right;
            }

            .black-contact{
                width: 100%;
                }
           
            
        
            .fondo_contact{
                width:80%;
                height: auto;
            }
            
        
            .contact-tit{
                font-size: 2rem;
            }
        
            .p_black-contact{
                
                font-size: 1.5rem;
            
            }
        






            }    
    

