    /* inicio @media <749px */

    @media all and (max-width:749px){

        .black-home{
            width: 100%;
            }
        
        
            .h2_black-home{
        
                width: 80%;
                text-align: right;
                float: right;
                margin-right: 10px;
                font-size: 15px;
                padding: 10px ;
        
                }
            
                .perfil{
                    
                    flex-direction: column;
                    display:column;
                            }

                .caja-foto{
                    width: 100px;
                    height:100px;
                   
                    margin-top: 20px;
                }

                .foto-perfil{
                border-radius: 50%;
                height: 95%;
                width: 95%;
                background-color:#293b3b;

                
            }

       
    


            .p_black-home{
                
                font-size: 2.3rem;
                line-height: 79.5%;
                text-align: right;
                margin-top: 30px;
                width: 100%;

            }
        
            .black-contact{
                width: 100%;
                }
           
            
        
            .fondo_contact{
                width:80%;
                height: auto;
            }
            
        
            .contact-tit{
                font-size: 1.5rem;
            }
        
            .p_black-contact{
                
                font-size: 1.2rem;
            
            }




    }