body{
    box-sizing: border-box;
   margin:0;
   padding:0;
position: relative;
width: auto;
height: 100vh;
margin-left: 0;
margin-right: 0;


background: #414141;
font-family: 'Bebas Neue', cursive;
}



.header-menu{

   padding:10px;
   display:flex;
   justify-content: center;
    align-items: center;
   width: 100%;
   flex-direction: row;
   align-items: center;

}




 .h1-menu{
text-align: center;
font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
font-size: 60pt;
line-height:43pt;
color: #FFFFFF;
margin: 10px 40px;
}

.inicio-menu{
     text-align: center;
position: relative;
font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
font-size: 60pt;
line-height:43pt;
color: #FFFFFF;
text-decoration: none
    }
    
    .inicio-menu:hover{
    transform:scale(1.15);
        text-shadow: 4px 4px 1px rgba(0, 0, 0, 0.25);
    transition: all .5s;
    }
    

.navbar_580{
    display: none;

}

.botonotes{
    position: relative;
    width: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin: 0;
    padding: 0;
    height:150px;
}

.musico-menu{
display: flex;    
position: relative;
font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
font-size: 35px;
line-height:4vw;
margin:30px;

color: #FFFFFF;

transform: rotate(3deg);
}

.musico-menu:hover{
    top:-3px;
    color: #51C0DC;
text-shadow: 4px 4px 1px rgba(0, 0, 0, 0.25);
transition: all .5s;
}

.geologo-menu{
    
position: relative;

font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
font-size: 35px;
line-height:4vw;
margin:30px;

display: flex;
align-items: center;

color: #FFFFFF;

transform: rotate(-3deg);

}


.geologo-menu:hover{
    top:-3px;
    color: #52D9D9;
    text-shadow: 4px 4px 1px rgba(0, 0, 0, 0.25);
    transition: all .5s;

    }

.programador-menu{
    
position: relative;

font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
font-size: 35px;
line-height:4vw;
margin:30px;

display: flex;
align-items: center;

color: #FFFFFF;

transform: rotate(-1.5deg);

}

.programador-menu:hover{
    top:-3px;
    color: #E29270;
    text-shadow: 4px 4px 1px rgba(0, 0, 0, 0.25);
    transition: all .5s;

    }


    .contact-menu{
    
        position: relative;
        
        font-family: Bebas Neue;
        font-style: normal;
        font-weight: normal;
        font-size: 35px;
        line-height:4vw;
        margin:30px;
        
        display: flex;
        align-items: center;
        
        color: #FFFFFF;
        
        transform: rotate(3deg);
        
        }
        
        
        .contact-menu:hover{
            top:-3px;
            color: #9ED952;
            text-shadow: 4px 4px 1px rgba(0, 0, 0, 0.25);
            transition: all .5s;
        
            }
        




