body{
    box-sizing: border-box;
   margin:0;
   padding:0;
width: 100%;
height: auto;
margin-left: 0;
margin-right: 0;


background: #414141;
font-family: 'Bebas Neue', cursive;

}



.fondo-mus{
    position: absolute;
 
    width:80%;
    z-index: 1;
    transform: scaleX(-1); 
    max-width: 100%;
    right:0;
    height: auto;
    border-radius: 20px 0px 0px 0px;
}





.black-mus{
    position: relative;
    width: 80vw;
    background: #131313;
    border-radius: 20px;
    margin-left:auto;
    margin-right: auto;
    margin-bottom: 10px;
    padding: 0;
    padding-bottom: 40px;

}

.header_black-mus{  
    position: relative;  
    display:flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
    overflow: hidden;
}

.musico-tit{
    position: relative;
    display: inline-block;
    margin:0;
    margin-right: 10%;
    top: 10px;
    text-align: right;
    color: #51C0DC;
    font-style: normal;
    font-weight: normal;
    font-size: 5rem;
    background-color: #131313;
    padding:0px 15px;
    border-radius: 5px;
    z-index: 2;
    margin-bottom:20px;

}










.info-mus{
    position: relative;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    height:auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;  
    background-color: #2c2c2c;
    top: 10px;
    z-index: 1;
    padding:5px;
    
}

.info-mus h3{
    font-size: 1.6rem;
    color: #51C0DC;
    margin-left: 25px;
    margin-top: 0px;
    margin-bottom: 0px;
    font-weight: 100;
}

.info-mus p{
  
    font-size: 1.2rem;
    color: #ffffff;
    margin-right: 25px;
    font-weight: 100;
    text-align: right;

}

.grupos-mus{
    position:relative;
    width: 60%;
    text-align: right;
    top: 0px;
    right: 0px;
    
}

.h2_black-mus{
    
    font-style: italic;
    font-size: 1.3rem;
    transform: rotate(-5deg);
    margin:0 25px;
    font-weight: 100;

    padding-top: 20px;
    color: #ff0000;

    }

    .info_video-mus{
        position: relative;
        width: 80%;
        display: flex;
        flex-wrap: wrap;
        align-content: space-around;
        justify-content: space-evenly;
        border-radius: 5px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 10px;  
        background-color: #2c2c2c;
        top: 10px;
        
        
    }

    iframe{
        margin:10px;
        border-radius:5px;
        background-color: #202020;
    }





/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/

    /* inicio @media 750 a 960px */

    @media all and (min-width:750px) and (max-width:960px){

        
        
        .black-mus{
            width: 100%;
       
        }
        
        
        .musico-tit{
            font-size: 3.5rem;
            background-color: #131313;
            padding:0px 15px;
            border-radius: 5px;
            z-index: 2;
            margin-bottom:20px;
                
        }
        
        
        
        
        .botonera-mus{
            display: flex;
            float:right;
            justify-content: space-evenly;
            align-items: center;
            width: 40%;
            height: 45px;
            right: 0px;
            top: 10px;
            padding:0;
            background: #2c2c2c;
            border-radius: 6px 0px 0px 6px;
            list-style: none;
            z-index: 1;
        
        
        }
        
        .boton-mus a {
            
            font-size: 1.2rem;
        }
        
        
        .info-mus h3{
            font-size: 1.2rem;
        }
        
        .info-mus p{
          
            font-size: 1rem;
        
        }
        
        iframe{
            width: 90%;
            height:auto;
        }

                }    
        

    
    
    /*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/

    /* inicio @media <749px */

    @media all and (max-width:749px){

        .black-mus{
            width: 100%;
       
        }
        
        
        .musico-tit{
            font-size: 2.5rem;
            padding:0px 10px;
                
        }
        
        
        
        
        .botonera-mus{
            width: 60%;
            height: 35px;
        }
        
        .boton-mus a {
            
            font-size: 1rem;
        }
        
        
        .info-mus h3{
            font-size: 1rem;
        }
        
        .info-mus p{
          
            font-size: .8rem;
        
        }  
    
            
    
        iframe{
            width: 90%;
        }
    
    
    
    }    
    