body{
    box-sizing: border-box;
   margin:0;
   padding:0;
width: 100%;
height: auto;
margin-left: 0;
margin-right: 0;


background: #414141;
font-family: 'Bebas Neue', cursive;
}



.fondo-geo{
    position: absolute;
 
    width:80%;
    z-index: 1;
    transform: scaleX(-1); 
    max-width: 100%;
    right:0;
    height: auto;
    border-radius: 20px 0px 0px 0px;
}





.black-geo{
    position: relative;
    width: 80vw;
    background: #131313;
    border-radius: 20px;
    margin-left:auto;
    margin-right: auto;
    margin-bottom: 10px;
    padding: 0;
    padding-bottom: 40px;

}

.header_black-geo{  
    position: relative;  
    display:flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
    overflow: hidden;
}

.geologo-tit{
    position: relative;
    display: inline-block;
    margin:0;
    margin-right: 10%;
    top: 10px;
    text-align: right;
    color: #52D9D9;
    font-style: normal;
    font-weight: normal;
    font-size: 5rem;
    background-color: #131313;
    padding:0px 15px;
    border-radius: 5px;
    z-index: 2;
    margin-bottom:20px;

}




.botonera-geo{
    display: flex;
    float:right;
    justify-content: space-evenly;
    align-items: center;
    width: 40%;
    height: 45px;
    right: 0px;
    top: 10px;
    padding:0;
    background: #2c2c2c;
    border-radius: 6px 0px 0px 6px;
    list-style: none;
    z-index: 1;


}

.boton-geo a {
    
    font-size: 1.8rem;
    color: #fff;
    text-decoration: none;
}

.boton-geo a:hover{
    color: #52D9D9;
    cursor: pointer;
    transition: .5s ease all;
}






.info-geo{
    position: relative;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    height:auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;  
    background-color: #2c2c2c;
    top: 10px;
    z-index: 1;
    padding:5px;
    
}

.info-geo h3{
    font-size: 1.6rem;
    color: #52D9D9;
    margin-left: 25px;
    margin-top: 0px;
    margin-bottom: 0px;
    font-weight: 100;
}

.info-geo p{
  
    font-size: 1.2rem;
    color: #ffffff;
    margin-right: 25px;
    font-weight: 100;
    text-align: right;

}

.grupos-geo{
    position:relative;
    width: 60%;
    text-align: right;
    top: 0px;
    right: 0px;
    
}




