body{
    box-sizing: border-box;
   margin:0;
   padding:0;
width: 100%;
height: auto;
margin-left: 0;
margin-right: 0;


background: #414141;
font-family: 'Bebas Neue', cursive;

}



.fondo-prog{
    position: absolute;
 
    width:80%;
    z-index: 1;
    transform: scaleX(-1); 
    max-width: 100%;
    right:0;
    height: auto;
    border-radius: 20px 0px 0px 0px;
}





.black-prog{
    position: relative;
    width: 80vw;
    background: #131313;
    border-radius: 20px;
    margin-left:auto;
    margin-right: auto;
    margin-bottom: 10px;
    padding: 0;
    padding-bottom: 40px;

}

.header_black-prog{  
    position: relative;  
    display:flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
    overflow: hidden;
}

.programmer-tit{
    position: relative;
    display: inline-block;
    margin:0;
    margin-right: 10%;
    top: 10px;
    text-align: right;
    color: #E29270;
    font-style: normal;
    font-weight: normal;
    font-size: 5rem;
    background-color: #131313;
    padding:0px 15px;
    border-radius: 5px;
    z-index: 2;
    margin-bottom:20px;

}




.botonera-prog{
    display: flex;
    float:right;
    justify-content: space-evenly;
    align-items: center;
    width: 40%;
    height: 45px;
    right: 0px;
    top: 10px;
    padding:0;
    background: #2c2c2c;
    border-radius: 6px 0px 0px 6px;
    list-style: none;
    z-index: 1;


}

.boton-prog a {
    
    font-size: 1.8rem;
    color: #fff;
    text-decoration: none;
}

.boton-prog a:hover{
    color: #E29270;
    cursor: pointer;
    transition: .5s ease all;
}






.info-prog{
    position: relative;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    height:auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;  
    background-color: #2c2c2c;
    top: 10px;
    z-index: 1;
    padding:5px;
    
}


.info-prog h3{
    font-size: 1.6rem;
    color: #E29270;
    margin-left: 25px;
    margin-top: 0px;
    margin-bottom: 0px;
    font-weight: 100;
}

.info-prog p{
  
    font-size: 1.3rem;
    color: #ffffff;
    margin-right: 25px;
    font-weight: 100;
    text-align: right;

}

.grupos-prog{
    position:relative;
    width: 60%;
    text-align: right;
    top: 0px;
    right: 0px;
    
}


/* iconos */



.soft-prog{
    position: relative;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    height:auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;  
    background-color: #2c2c2c;
    top: 10px;
    z-index: 1;
    padding:5px;
    
    
}

.soft-prog h3{
    font-size: 1.6rem;
    color: #E29270;
    font-weight: 100;
    margin-top: 5px;
    margin-bottom: 15px;
}



.icons-prog{
    width: 80%;    
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap:10px;
    margin:5px 5px;

}

.icons-prog > img{
    width: 60px;
    height: auto;
    padding:5px;
    background-color: #1b1b1b;
    border-radius: 5px;
}

.certificaciones{
    position: relative;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    height:auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;  
    background-color: #2c2c2c;
    top: 10px;
    z-index: 1;
    padding:15px;
    padding-top:5px;
}



.certificaciones h3{
    font-size: 1.6rem;
    color: #E29270;
    font-weight: 100;
    margin-top: 5px;
    margin-bottom: 10px;
}

.certificado{
    text-decoration: none;
    color: white;
    background-color:#E29270;
    width: auto;
    text-align: center;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 1.2rem;
}
.certificadoFinal{
    text-decoration: none;
    color: white;
    background-color:#e75e23;
    width: auto;
    text-align: center;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 1.2rem;
}


.certificado:hover{
    
    color: white;
    background-color:#636363;
    outline: solid 1px #E29270;
    cursor: pointer;
    transition: all .5s;

}
.certificadoFinal:hover{
    
    color: e75e23;
    background-color:#636363;
    outline: solid 1px #e75e23;
    cursor: pointer;
    transition: all .5s;

}



/*PROYECTOS*/
      



.link-proyecto{
    width: 40%;
    height: 60px;
    display:flex;
    justify-content: space-evenly;
    align-items: center;
    

}

.link-proyecto a{
    display:flex;
    width: 30%;
    font-size: 1.8rem;
    color:#fff;
    text-decoration: none;
    height: 100%;
    background-color: #E29270 ;
    padding:10px;
    align-items: center;
    justify-content: center;
    border-radius: 5px;

}

.link-proyecto img{
    width: auto;
    height: 100%;
    padding:3px;

}

.link-proyecto a:hover{
    transform: scale(1.1);
    background-color: #636363;
    outline: solid 1px #E29270;
    cursor: pointer;
    transition: all .5s;


}

.info-proyecto{
    width: 80%;
    min-height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5px;  
    background-color: #4d4d4d;
    top: 10px;
    z-index: 1;
    padding:5px;
    margin-bottom: 20px;

    
}

.p-proyecto{
    width: 60%;
    font-size: 1.2rem;
    color: #ffffff;
    margin-right: 25px;
    font-weight: 100;
    text-align: right;
}



