

.toggleContainer{
  color: rgb(255, 255, 255);
  display: flex;
   align-items: center;
   border-radius: 15px;
   padding: .2em;
   justify-content: center;
    background-color: rgb(22, 22, 22);
    font-weight:100;
    width: 60px;
    height:50px;
    font-size: 1.3em;
    margin: 0 0.5em ;
    margin-top:3px;
    cursor: pointer;
    text-decoration: none;
    list-style:none
  }
  
  .toggleContainer:hover{
    color: #ffffff;
    background-color:#e06907;
    transition: ease-in-out .25s;

}


 /* inicio @media 750 a 960px */

 @media all and (min-width:750px) and (max-width:960px){
  .toggleContainer{
      width: 50px;
      height:45px;
      font-size: 1.2em;
    }
  
  }

    /* inicio @media <749px */

    @media all and (max-width:749px){
      .toggleContainer{
        width: 40px;
        height:35px;
        font-size: 1.1em;
      }
    
    }