body, #root{
    box-sizing: border-box;
   margin:0;
   padding:0;
width: 100%;
height: auto;
margin-left: 0;
margin-right: 0;


background: #414141;
font-family: 'Bebas Neue', cursive;
}


    .black-contact{
        width: 80%;
        height: auto;
        background: #131313;
        border-radius: 20px ;
        z-index: 0;
        padding: 20px;
        margin: 0 auto;
        margin-bottom:40px;
           
            
        }
   
    

    .fondo_contact{
        position: absolute;
        width:80%;
        height: auto;
        z-index: -1;
        transform: scaleX(-1); 

        top: 0px;
        right:0;
        border-radius: 20px 0px 0px 0px;
    }
    

    .contact-tit{
        position: relative;
        display: inline-block;
        margin:0;
        margin-right: 10%;
        top: 10px;
        text-align: right;
        color: #9ED952;
        font-style: normal;
        font-weight: normal;
        font-size: 5rem;
        background-color: #131313;
        padding:0px 15px;
        border-radius: 5px;
        z-index: 2;
        margin-bottom:20px;
    
    }

    .div_form{
        position: relative;
        width: 80%;
        display: flex;
        justify-content: flex-start;
        border-radius: 5px;
        height:auto;
        margin-left: auto;
        margin-right: auto;
        margin-top: 10px;  
        background-color: #2c2c2c;
        top: 10px;
        z-index: 1;
        padding:5px;
    }
.form_area{
    width: 100%;
}

.campo_form{
    width:100%;
    position: inherit;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border-radius: 5px;
    height:auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;  
    background-color: #2c2c2c;
    top: 10px;
    z-index: 1;
    padding:5px;
}

    .label{
        width: 20%;
        font-size: 1.6rem;
    color: #9ED952;
    margin-top: 0px;
    margin-bottom: 0px;
    font-weight: 100;
    text-align: center;

    
    }

    .campo{
    transition: width 0.4s ease-in-out;
    width: 40%;
    font-size: 1.2rem;
    color: #000000;
    text-align: center;
    border-radius:5px;
    height: 40px;
    border: solid 1px #9ED952;
    margin-bottom: 10px;
    font-family: 'Oswald', sans-serif;        
    }

    .campo_txt{
        transition: width 0.4s ease-in-out;
        width: 40%;
        font-size: 1.2rem;
        color: #000000;
        font-weight: 100;
        text-align: center;
        border-radius:5px;
        height: auto;
        border: solid 1px #9ED952;
        font-family: 'Oswald', sans-serif;
        margin-bottom: 10px;
        padding-top: 10px;
        
    }

    .campo:focus, .campo_txt:focus{
        width: 80%;

        background-color: lightblue;
        border: 3px solid rgb(227, 252, 2);
    
        
        }
    


    .cont_btn{
        display:flex;
        justify-content: center;
        flex-direction: column;
        margin:15px 0px;
        width:100%;
        align-items: center;

    }
    .boton{
        width: 30%;
        background: #9ED952;
        padding:5px 10px;
        font-size: 2rem;
        border-bottom: none;
        border-radius:5px;
        
    }

    .boton:hover{
        width: 30%;
        background: #8ffc02;
        padding:5px 10px;
        font-size: 2rem;
        border-bottom: none;
        border-radius:5px;
        margin-left: 0;
        margin-right: 0;
        
    }

    #enviado{
        visibility: hidden;
        color:#73ff00;
        font-size: 1.5rem;
        width: 100%;
        text-align: center;
        margin-top: 15px;

    }
        .error{
            width: 70%;
            color:#ff8800;
            border: none;
            font-size: 1.2rem;
            text-align: center;


        }

