    /* inicio @media 750 a 960px */

    @media all and (min-width:750px) and (max-width:960px){

         

            .black-contact{
                width: 100%;
                }
           
        
            .contact-tit{
                font-size: 3.5rem;
            }
        
        
            .label{
                font-size: 1.4rem;
            }
        
            .campo{
            width: 60%;
            font-size: 1.1rem;
            height: 35px;
           }
        
            .campo_txt{
                width: 60%;
                font-size: 1.1rem;
            }
        
            .campo:focus, .campo_txt:focus{
                width: 80%;
                }
            
               
            .boton{
                font-size: 1.5rem;
                
            }
        
            .boton:hover{
                font-size: 1.5rem;
                
            }
        
            #enviado{
                font-size: 1.3rem;
        
            }
        

            .error{
                width:60%;
                font-size: 1rem;
                
    
            }
    



            }    
    

                /*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
/*!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/

    /* inicio @media <749px */

    @media all and (max-width:749px){

          
        .black-contact{
            width: 100%;
            }
       
        .contact-tit{
            font-size: 2.5rem;
        }
    
    
        .label{
            font-size: 1.2rem;
        }
    
        .campo{
        width: 70%;
        font-size: 1rem;
        height: 25px;
       }
    
        .campo_txt{
            width: 70%;
            font-size: 1rem;
        }
    
        
           
        .boton{
            font-size: 1.2rem;
            padding:3px 7px;

            
        }
    
        .boton:hover{
            font-size: 1.2rem;
            padding:3px 7px;

            
        }
    
        #enviado{
            font-size: 1.2rem;
    
        }
        .error{
            width:60%;
            font-size: 0.8rem;

        }

    }